.case_ass{
    padding: 20px;
    padding-bottom: 20px;
    background: #fff;
}

.case_ass > .case_content > li{
    height: 133px;
    margin-bottom: 20px;
    background: #eee;
    padding: 20px;
    border-radius: 8px;
}
.case_ass > .case_content > li > a{
    display: block;
    height: 133px;
    display: flex;
}
.case_ass > .case_content > li > a > img{
    width: 200px;
    height: 133px;
    border-radius: 8px;
}
.case_ass > .case_content > li > a > div{
    flex: 0 1 auto;
    height: 133px;
    box-sizing: border-box;
    padding: 0 16px;
}

.case_ass > .case_content > li > a > div > h4{
    line-height: 40px;
    font-size: 18px;
    color: #000;
}
.case_ass > .case_content > li > a > div > p{
    font-size: 14px;
    line-height: 20px;
    color: #666;
}